import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/all',
      name: 'all',
      component: () => import('./views/AllCoins.vue')
    },
    {
      path: '/about',
      name: 'about',
      component: () => import('./views/About.vue')
    },
    {
      path: '/crypto',
      name: 'crypto',
      component: () => import('./views/HomeCryptoCoin.vue'),
      children: [
        {
          path: 'compare-crypto',
          name: 'cryptoCompare',
          props: true,
          component: () => import('./components/CryptoCoinCompareComponent.vue')
        }
      ]
    },
    {
      path: '/currency',
      name: 'currency',
      component: () => import('./views/HomeCurrency.vue'),
      children: [
        {
           path: 'compare-currency',
           name: 'currencyCompare',
           component: () => import('./components/CurrencyCompareComponent.vue')
        },   
      ]
    },   

         
  ]
})
